:root {
  --primary-button-bc-color: #960505;
  --border-color: #350000;
  --result-bc-color: #e4dce2;
  --result-font-color: #000;
  --operators-bc-color: #ca3608;
  --button-font-color: #fff;
  --cell-height: 50px;
  --cell-width: 125px;
  --buttons-default-font-size: 20px;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html,
body,
#root {
  background-color: #7d22a7;
  height: 100%;
}
